"use client";
import { useState } from 'react';

// Types for our data
type Goal = 'lose' | 'gain' | 'maintain';
type Preference = 'weightlift' | 'cardio' | 'both';

export default function HealthQuiz() {
  const [step, setStep] = useState(1);
  const [data, setData] = useState({
    gender: '',
    age: '',
    feet: '',
    inches: '',
    weightLbs: '',
    goal: '' as Goal | '',
    preference: '' as Preference | '',
    benchPress: '',
    pushups: '',
    mileTime: '',
  });

  // Validation: Check if the current step's required fields are filled
  const isStepValid = () => {
    switch (step) {
      case 1: return data.gender !== '';
      case 2: return data.age !== '' && Number(data.age) > 0;
      case 3: return data.feet !== '' && data.inches !== '' && data.weightLbs !== '';
      case 4: return data.goal !== '';
      case 5: return data.preference !== '';
      case 6: 
        // Bench is only required if they chose weightlift/both
        const needsBench = data.preference === 'weightlift' || data.preference === 'both';
        const benchValid = needsBench ? data.benchPress !== '' : true;
        return benchValid && data.pushups !== '' && data.mileTime.includes(':');
      default: return false;
    }
  };

  const nextStep = () => {
    if (isStepValid()) setStep(prev => prev + 1);
  };

  // Prevent non-numeric typing
  const handleNumericInput = (val: string, key: string) => {
    const numericValue = val.replace(/[^0-9]/g, '');
    setData({ ...data, [key]: numericValue });
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-slate-50 p-4 font-sans">
      <div className="w-full max-w-md bg-white rounded-3xl shadow-2xl p-8 border border-slate-100">
        
        {/* Step 1: Gender */}
        {step === 1 && (
          <div className="space-y-6 animate-in fade-in duration-500">
            <h2 className="text-2xl font-black text-center text-slate-800">I AM...</h2>
            <div className="grid grid-cols-2 gap-4">
              {['male', 'female'].map((g) => (
                <button 
                  key={g}
                  onClick={() => { setData({...data, gender: g}); setStep(2); }}
                  className={`p-6 border-2 rounded-2xl capitalize font-bold transition-all cursor-pointer ${data.gender === g ? 'border-blue-600 bg-blue-50 text-blue-600' : 'border-slate-100 hover:border-blue-300'}`}
                >
                  {g}
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Step 2: Age */}
        {step === 2 && (
          <div className="space-y-6">
            <h2 className="text-2xl font-black text-slate-800">WHAT IS YOUR AGE?</h2>
            <input 
              type="text" 
              inputMode="numeric"
              value={data.age}
              placeholder="e.g. 25"
              className="w-full p-4 text-lg border-2 border-slate-100 rounded-2xl focus:border-blue-500 outline-none transition"
              onChange={(e) => handleNumericInput(e.target.value, 'age')}
            />
            <button 
              disabled={!isStepValid()}
              onClick={nextStep} 
              className="w-full bg-blue-600 disabled:bg-slate-300 text-white p-4 rounded-2xl font-bold shadow-lg shadow-blue-200 cursor-pointer active:scale-95 transition"
            >
              Continue
            </button>
          </div>
        )}

        {/* Step 3: Height (Feet/Inches) & Weight (Lbs) */}
        {step === 3 && (
          <div className="space-y-6">
            <h2 className="text-2xl font-black text-slate-800">MEASUREMENTS</h2>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-500 ml-1">Feet</label>
                <input type="text" inputMode="numeric" placeholder="5" value={data.feet} className="w-full p-4 border-2 border-slate-100 rounded-2xl" onChange={(e) => handleNumericInput(e.target.value, 'feet')}/>
              </div>
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-500 ml-1">Inches</label>
                <input type="text" inputMode="numeric" placeholder="10" value={data.inches} className="w-full p-4 border-2 border-slate-100 rounded-2xl" onChange={(e) => handleNumericInput(e.target.value, 'inches')}/>
              </div>
            </div>
            <div className="space-y-2">
              <label className="text-sm font-bold text-slate-500 ml-1">Weight (lbs)</label>
              <input type="text" inputMode="numeric" placeholder="185" value={data.weightLbs} className="w-full p-4 border-2 border-slate-100 rounded-2xl" onChange={(e) => handleNumericInput(e.target.value, 'weightLbs')}/>
            </div>
            <button disabled={!isStepValid()} onClick={nextStep} className="w-full bg-blue-600 disabled:bg-slate-300 text-white p-4 rounded-2xl font-bold cursor-pointer">Continue</button>
          </div>
        )}

        {/* Step 4: Goal */}
        {step === 4 && (
          <div className="space-y-3">
            <h2 className="text-2xl font-black text-center text-slate-800">MY GOAL</h2>
            {['lose', 'gain', 'maintain'].map((g) => (
              <button 
                key={g}
                onClick={() => { setData({...data, goal: g as Goal}); setStep(5); }}
                className="w-full p-5 border-2 border-slate-100 rounded-2xl text-left capitalize font-bold hover:bg-slate-50 cursor-pointer transition"
              >
                {g} Weight
              </button>
            ))}
          </div>
        )}

        {/* Step 5: Preference */}
        {step === 5 && (
          <div className="space-y-3">
            <h2 className="text-2xl font-black text-center text-slate-800">I PREFER...</h2>
            {['weightlift', 'cardio', 'both'].map((p) => (
              <button 
                key={p}
                onClick={() => { setData({...data, preference: p as Preference}); setStep(6); }}
                className="w-full p-5 border-2 border-slate-100 rounded-2xl text-left capitalize font-bold hover:bg-slate-50 cursor-pointer transition"
              >
                {p}
              </button>
            ))}
          </div>
        )}

        {/* Step 6: Final Assessment */}
        {step === 6 && (
          <div className="space-y-6">
            <h2 className="text-2xl font-black text-slate-800 text-center">PERFORMANCE</h2>
            
            {(data.preference === 'weightlift' || data.preference === 'both') && (
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-500 ml-1">Max Bench Press (lbs)</label>
                <input type="text" inputMode="numeric" placeholder="Optional" value={data.benchPress} className="w-full p-4 border-2 border-slate-100 rounded-2xl" onChange={(e) => handleNumericInput(e.target.value, 'benchPress')}/>
              </div>
            )}

            <div className="space-y-2">
              <label className="text-sm font-bold text-slate-500 ml-1">Pushups in 1 Minute</label>
              <input type="text" inputMode="numeric" placeholder="Reps" value={data.pushups} className="w-full p-4 border-2 border-slate-100 rounded-2xl" onChange={(e) => handleNumericInput(e.target.value, 'pushups')}/>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-bold text-slate-500 ml-1">1-Mile Run Time (MM:SS)</label>
              <input 
                type="text" 
                placeholder="09:30" 
                value={data.mileTime}
                className="w-full p-4 border-2 border-slate-100 rounded-2xl" 
                onChange={(e) => setData({...data, mileTime: e.target.value})}
              />
            </div>

            <button 
              disabled={!isStepValid()}
              onClick={() => alert("Analyzing your stats...")}
              className="w-full bg-green-600 disabled:bg-slate-300 text-white p-5 rounded-2xl font-black text-lg cursor-pointer shadow-lg shadow-green-100 transition"
            >
              FINISH ASSESSMENT
            </button>
          </div>
        )}

        {/* Progress Dots */}
        <div className="flex justify-center gap-2 mt-8">
          {[1,2,3,4,5,6].map(s => (
            <div key={s} className={`h-1.5 rounded-full transition-all ${step === s ? 'w-8 bg-blue-600' : 'w-2 bg-slate-200'}`} />
          ))}
        </div>
      </div>
    </div>
  );
}